import { createRouter, createWebHistory } from 'vue-router';

const router = createRouter({
    history: createWebHistory(),
    routes: [
        {
            path: '/',
            component: () => import('../components/home/HomePage.vue'),

        },
        {
            name: 'Cours',
            path: '/cours',
            component: () => import('../components/result/cours/CoursResultPage.vue'),
            props: true
        },
        {
            path: '/admin',
            name: 'admin',
            component: () => import('../components/admin/AdminPage.vue'),
            children: [
                {
                    path: '/admin/cours',
                    name: 'cours',
                    component: () => import('../components/admin/page/AdminCours.vue')
                },
                {
                    path: '/admin/forum',
                    name: 'forum',
                    component: () => import('../components/admin/page/AdminForum.vue')
                },
                {
                    path: '/admin/specialite',
                    name: 'specialite',
                    component: () => import('../components/admin/page/AdminSpecialite.vue')
                }
            ]
        },
        {
            path: '/login',
            component: () => import('../components/login/LoginPage.vue')
        }
    ]
})
router.beforeEach((to, from, next) => {
    const publicPages = ['/', '/login', '/cours'];
    const authRequired = !publicPages.includes(to.path);
    const loggedIn = sessionStorage.getItem('admin');
      if (authRequired && !loggedIn) {
      next('/');
    } else {
      next();
    }
});

export default router;