<template>
    <div id="test" class="header-page" >
      <div class="image-top">
        <a href="https://www.instagram.com/delmoo_off/" target="_blank">
          <img src="../../assets/top_logo.png" alt="">
        </a>
      </div>
      <div id="nav" class="navigator">
        <HomeSelectorVue @click="navigate('first-page')"></HomeSelectorVue>
        <CoursSelectorVue @click="navigate('second-page')"></CoursSelectorVue>
        <ForumSelectorVue @click="navigate('third-page')"></ForumSelectorVue>
        <SpecialitySelectorVue @click="navigate('fourth-page')"></SpecialitySelectorVue>
        <CalendrierSelectorVue @click="navigate('fifth-page')"></CalendrierSelectorVue>
        <AboutSelectorVue @click="navigate('sixth-page')"></AboutSelectorVue>
      </div>
      <div class="buttons">
        <button class="admin" @click="toAdminAccess">Accès Admin</button>
        <div class="show">
          <SideBarVue v-bind:isMobile="true" v-bind:releve="releve" v-bind:toggleModale="toggleModale"></SideBarVue>
        </div>
      </div>
    </div>  
</template>

<script>
import HomeSelectorVue from './selectors/HomeSelector.vue';
import CoursSelectorVue from './selectors/CoursSelector.vue';
import CalendrierSelectorVue from './selectors/CalendrierSelector.vue';
import AboutSelectorVue from './selectors/AboutSelector.vue';
import ForumSelectorVue from './selectors/ForumSelector.vue';
import SpecialitySelectorVue from './selectors/SpecialitySelector.vue';
import SideBarVue from './sideBar/SideBar.vue';

export default {
  name: "HeaderPage",
  components: {
    HomeSelectorVue,
    CoursSelectorVue,
    CalendrierSelectorVue,
    AboutSelectorVue,
    ForumSelectorVue,
    SpecialitySelectorVue,
    SideBarVue
  },
  data() {

    return {
      releve: false,
    }
  },
  methods: {
    toggleModale: function () {
      this.releve = !this.releve
    },
    navigate: function(page) {
      try {
        var target = document.getElementById(page);
        target.scrollIntoView({behavior: 'smooth'});
      } catch {
        this.$router.push({path: '/'});
      }
    },
    toAdminAccess: function () {

      if(sessionStorage.getItem('admin')){
        this.$router.push({path: '/admin/cours'})
      }else {
        this.$router.push({path: '/login'});
      }
    }

  }
}
</script>

<style lang="scss">
.header-page {

  background-color: var(--dark);
  position: fixed;
  width: 100%;
  height: 6vh;
  top: 0;
  right: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;

  .image-top {
    margin-left: 1vw;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      height: var(--top-logo-size-mobile);
      width: var(--top-logo-size-mobile);
    }
  }

  .navigator {
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;

    @media screen and (max-width: 1400px) {
      visibility: hidden;
    }

  }

  .buttons {

    .admin {
      visibility: visible;
      position: absolute;
      right: 0;
      top: 1vh;
      border-radius: 5px 5px;
      background: none;
      border: 0px;
      color: rgb(255, 255, 255);
      cursor: pointer;
      height: 4vh;
      width: 7vw;
      font-size: var(--text-size-header-title);
      margin-right: 1vw;
      transition: all var(--transition-duration) ease-out;

      &:hover {
        background-color: var(--selected);
        border-radius: 5px 5px;
        background-size: 100% .1rem;
      }
    }

    .show {
      visibility: hidden;
    }

    @media screen and (max-width: 1400px) {
      .admin {
        visibility: hidden;
      }
      .show {
        visibility: visible;
      }
    }

  }

  .pageNumbers {
    counter-reset: currentPage;
  }

  .pageNumbers div:before {
    counter-increment: currentPage;
    content: "Page " counter(currentPage) " of ";
  }
}
</style>